.App{text-align: center;}
.black-nav{
  background: #ccc;
  padding: 20px;
  box-sizing: border-box;
  color: palevioletred;
  display: flex;
  justify-content: flex-start;
}
div{box-sizing: border-box;}
.list{
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid gray;
}
.list>h4:nth-child(1){position: relative; padding-left: 10px;}
.list>h4:nth-child(1):after{position: absolute;content: ""; width: 3px; height: 15px; background: palevioletred; top: 3px; left: 0;}
button{
  border: none;
  background: palevioletred;
  color: #fff;
  padding: 20px 40px;
  margin: 10px;
}
.modal{
  margin: 20px;
  padding: 20px;
  background: palevioletred;
  color: #fff;
}
span{
  padding: 0 5px;
}
input{padding: 10px;}